export const HOST = 'https://api.peopleofeden.com';

export default async function callApi(endpoint, method = 'get', body) {
    const url = `${HOST}/${endpoint}`;
    console.log('url', url);
    const headers = {
        'content-type': 'application/json',
    }

    return await fetch(url, {headers, method, body: JSON.stringify(body)})
        .then(async (response) => {
            if (!response.ok) {
                return response.json()
                    .then((json) => {
                        return Promise.reject(json)
                    });
            }
            return response.json();
        });
}
