import {ConnectButton} from "@rainbow-me/rainbowkit";
import React, {useEffect, useState} from "react";
import {useMediaQuery} from "react-responsive";
import {useAccount} from "wagmi";

import CustomModal from "../../components/Modal";

import "../../customCss/Custom-Slider.css"
import Eth_Logo_Black from "../../images/icons/Eth_Logo_Black.svg";
import Maha_logo_White from "../../images/icons/Maha_logo_White.svg";

import ModalClose from "../../images/icons/ModalClose.svg";
import faq_divider_mob from "../../images/vectors/faq_divider_mob.svg";
import modal_divider from "../../images/vectors/modal_divider.svg";
import {useGetAccount} from "../../utils/NetworksCustomHooks";
import FailureModal from "./FailureModal";
import MintingInProgressModal from "./MintingInProgressModal";
import "./MintModal.css"
import MintWithETH from "./MintWithETH";

import MintWithMaha, {ITxStatus} from "./MintWithMaha";
import SuccessfulMintModal from "./SuccessfulMintModal";

interface IProps {
  open: boolean;
  handleClose: () => void;
}

const MintingModal = (props: IProps) => {
  const {isConnected} = useAccount();
  const account = useGetAccount();
  const isMobile = useMediaQuery({query: '(max-width: 768px)'})
  const [mintWithToken, setMintWithToken] = useState<string>('MAHA')
  const [txStatus, setTxStatus] = useState<ITxStatus>('initial');
  const [nftId, setNFTId] = useState<string>('');
  const [failureMsg, setFailureMsg] = useState<string | undefined>('');

  const handleChangeCheck = (event: any) => {
    let isChecked = event.target.checked
    if (isChecked) {
      setMintWithToken("ETH")
    } else {
      setMintWithToken("MAHA")
    }
  }

  useEffect(() => {
    if (!props.open) {
      setTxStatus('initial');
      setMintWithToken('MAHA')
    }
  }, [props.open])

  return (
    <div>
      <MintingInProgressModal open={txStatus === 'in-progress'} handleClose={() => setTxStatus('')}/>
      <SuccessfulMintModal open={txStatus === 'successful'} nftid={nftId} handleClose={() => {
        setTxStatus('');
        props.handleClose();
        window.location.reload();
      }}/>
      <FailureModal open={txStatus === 'not-successful'} handleClose={() => props.handleClose()}
                    msg={failureMsg === '' ? undefined : failureMsg}/>
      <CustomModal open={props.open && txStatus === 'initial'} onClose={props.handleClose}>
        <div>
          <div className='mint-modal-heading'>
            <div>MINT NFT</div>
            <div className='pointer' onClick={props.handleClose}><img src={ModalClose} alt={'ModalClose'}/></div>
          </div>
          <div className='text-center mb16'>
            {
              isMobile ?
                <img src={faq_divider_mob} alt={'faq_divider_mob'} className={'responsiveImg'}/> :
                <img src={modal_divider} alt={'modal_divider'} className={'responsiveImg'}/>
            }
          </div>
          <div>
            <div style={{padding: 0, margin: 0}}>
              <input type="checkbox" onChange={handleChangeCheck} id="switch"/>
              <div className="app">
                <div className="body">
                  <div className="content">
                    <label htmlFor="switch">
                      <div className="toggle"></div>
                      <div className="names">
                        <div className="light">
                          <div><img src={Maha_logo_White} alt={'Maha_logo_White'}/></div>
                          <div>MAHA</div>
                        </div>
                        <div className="dark">
                          <div><img src={Eth_Logo_Black} alt={'Eth_Logo_Black'}/></div>
                          <div>ETH</div>
                        </div>
                      </div>
                    </label>
                  </div>
                </div>
              </div>
            </div>
            {
              !isConnected && !account
                ? (
                  <div className={'mint-modal-loading'}>
                    <div className={'enough-maha-error'}>Hey you need to connect you wallet to get your citizenship.</div>
                    <div className={'loading-cta'}>
                      <div data-rk={"true"} className={'mint-nft-btn'}><ConnectButton/></div>
                    </div>
                  </div>
                )
                : mintWithToken === 'MAHA'
                  ? <MintWithMaha txStatus={txStatus} setTxStatus={(value, nftId, msg) => {
                    setTxStatus(value);
                    setNFTId(nftId || '')
                    setFailureMsg(msg)
                  }}/>
                  : <MintWithETH txStatus={txStatus} setTxStatus={(value, nftIdm, msg) => {
                    setTxStatus(value);
                    setNFTId(nftId || '')
                    setFailureMsg(msg)
                  }}/>
            }
          </div>
        </div>
      </CustomModal>
    </div>
  )
}

export default MintingModal;
