import {useMediaQuery} from "react-responsive"
import shapeMob from "../../../images/icons/shape-mob.png"

import shape from "../../../images/icons/shape.png"
import HorizontalLine2Mob from "../../../images/vectors/HorizontalLine2Mob.png"
import HorizontalLineMob from "../../../images/vectors/HorizontalLineMob.png"
import HorizontalLine from "../../../images/vectors/Line6.1.png"
import VerticalLine from "../../../images/vectors/Line6.2.png"
import HorizontalLine2 from "../../../images/vectors/Line6.3.png"
import VerticalLineMob from "../../../images/vectors/VerticalLineMob.png"

const HeroSectionCard = (props: any) => {
  const {heading, subheading, img, isLastCard} = props
  const isMobile = useMediaQuery({query: '(max-width: 768px)'})

  return (
    <div id={"HeroSectionCard"}>
      <img src={isMobile ? shapeMob : shape} alt={"shape"}
           style={{position: 'absolute', top: 0, left: isMobile ? -8 : -22}}/>
      <img src={isMobile ? HorizontalLineMob : HorizontalLine}
           style={{position: 'absolute', top: isMobile ? 6 : 22, left: 22}}/>
      <div style={{padding: isMobile ? '28px' : '50px 0px 60px 30px', maxWidth: '369px'}}>
        <img src={img} alt={"front_coin2"} style={{display: 'flex', margin: isMobile ? '0px auto 34px auto' : ''}}/>
        <div className={"cardheading"}>{heading}</div>
        <div className={"label-3 cardsubheading"}>{subheading} </div>
      </div>
      <img src={isMobile ? VerticalLineMob : VerticalLine} alt={"VerticalLine"}
           style={{position: 'absolute', top: isMobile ? 30 : 48, left: 0}}/>
      <img src={isMobile ? shapeMob : shape} alt={"shape"}
           style={{position: 'absolute', bottom: isMobile ? 15 : '0px', left: isMobile ? -8 : -22}}/>
      <img src={isMobile ? HorizontalLine2Mob : HorizontalLine2} alt={"HorizontalLine2"} className={""}
           style={{position: 'absolute', bottom: '20px', right: isMobile ? 8 : 22}}/>
      {
        isLastCard || isMobile ?
          <div>
            <img src={isMobile ? shapeMob : shape} alt={"shape"}
                 style={{position: 'absolute', top: 0, right: isMobile ? -8 : -22}}/>
            <img src={isMobile ? VerticalLineMob : VerticalLine} alt={"VerticalLine"}
                 style={{position: 'absolute', top: isMobile ? 30 : 48, right: '0px'}}/>
            <img src={isMobile ? shapeMob : shape} alt={"shape"}
                 style={{position: 'absolute', bottom: isMobile ? 15 : '0px', right: isMobile ? -8 : -22}}/>
          </div>
          :
          <div/>
      }
    </div>
  )
}

export default HeroSectionCard
