// import {useWallet} from "use-wallet";
import {BigNumber} from "ethers";
import {useCallback, useEffect, useState} from "react";
import {useAccount} from "wagmi";
import {useGetActiveChainId} from "../../../state/chains/hooks";
import callApi from "../../../utils/apiCaller";
import useCore from "../../useCore";

export interface INFTDetailsValue {
  id: BigNumber;
  lockEnds: BigNumber;
  lockAmount: BigNumber;
  lockStarts: BigNumber;
  lockValue: BigNumber;
  ownedBy: string;
  userNft: IApiNFTDetails | null;
}

export interface ITraits {
  _id: string;
  category: string;
  trait: string
}

export interface IApiNFTDetails {
  _id: string;
  nftId: number;
  history: number;
  mahaxAmount: number;
  layers: object[];
  imageUrl: string;
  name: string;
  attributes: ITraits[];
}

export interface INFTDetails {
  isLoading: boolean;
  value: INFTDetailsValue | null;
}

export const Initial_Loading = {
  isLoading: true,
  value: null,
}

export const Initial_Non_Loading = {
  isLoading: false,
  value: null,
}

const useGetNFTDetails = (nftId: number) => {
  const [value, setValue] = useState<INFTDetails>(
    Initial_Loading
  );

  const core = useCore();
  const {address: account} = useAccount()

  const chainId = useGetActiveChainId();
  const contract = core.getVestingContract(chainId);

  const fetchData = useCallback(async () => {
    if (!account) {
      setValue(Initial_Non_Loading);
      return;
    }
    var userNft: IApiNFTDetails | null = null;
    try {
      userNft = await callApi(`nft/${nftId.toString()}`, 'get')
    } catch (e) {
      console.log('failed to fetch userNft api data', e);
    }
    const locked = await contract.locked(BigNumber.from(nftId))
    const lockValue = await contract.balanceOfNFT(BigNumber.from(nftId))
    const ownedBy = await contract.ownerOf(nftId);

    const data = {
      id: BigNumber.from(nftId),
      lockEnds: locked[1],
      lockAmount: locked[0],
      lockStarts: locked[2],
      lockValue: lockValue,
      ownedBy: ownedBy,
      userNft: userNft
    }

    setValue({
      isLoading: false,
      value: data,
    })
  }, [account, contract, nftId]);

  useEffect(() => {
    if (core) {
      setValue(Initial_Loading);
      fetchData().catch((err) => {
        setValue(Initial_Non_Loading);
        console.error(`Failed to fetch nft details: ${err.stack}`);
      });
    }
  }, [core, account, nftId, fetchData]);

  return value;
};

export default useGetNFTDetails;
