import React from "react";
import {useMediaQuery} from "react-responsive";
import Loader from 'react-spinners/ScaleLoader';
import Button from "../../components/Button";
import CustomModal from "../../components/Modal";
import useGetNFTDetailsFromAPI from "../../hooks/state/Minting/useGetNFTDetailsFromAPI";
import ModalClose from "../../images/icons/ModalClose.svg";
import faq_divider_mob from "../../images/vectors/faq_divider_mob.svg";
import modal_divider from "../../images/vectors/modal_divider.svg";
import placeholder from "../../images/maharaja/placeholder.png";

interface IProps {
  open: boolean;
  handleClose: () => void;
  nftid: string;
}

const SuccessfulMintModal = (props: IProps) => {
  const isMobile = useMediaQuery({query: '(max-width: 768px)'});

  const data = useGetNFTDetailsFromAPI(Number(props.nftid));

  return (
    <CustomModal open={props.open} onClose={props.handleClose}>
      <div>
        <div className='mint-modal-heading'>
          <div>{props.nftid? `CITIZEN #${props.nftid}`: `CITIZENSHIP MINTED`}</div>
          <div className='pointer' onClick={props.handleClose}><img src={ModalClose} alt={'ModalClose'}/></div>
        </div>
        <div className='text-center mb16'>
          {
            isMobile ?
              <img src={faq_divider_mob} alt={'faq_divider_mob'} className={'responsiveImg'}/> :
              <img src={modal_divider} alt={'modal_divider'} className={'responsiveImg'}/>
          }
        </div>
        <div>
          <div className='nft-revealed-image'>
            {data.isLoading
              ? <Loader height={84} width={4}/>
              : <img className='mb16' src={placeholder} alt="gif"/>
            }
          </div>
          <div className='modal-content-heading text-center mb16'>
            Your new citizen would be revealed in sometime. You can check the same here <a className={'links'} href={'https://peopleofeden.com/#/account'}>peopleofeden.com</a>.
          </div>
          <div className='flex-space-between share-on-twitter'>
            <Button
              onClick={() => {
                if (props.nftid) window.open(`https://opensea.io/assets/ethereum/0xbdd8f4daf71c2cb16cce7e54bb81ef3cfcf5aacb/${props.nftid}`)
                else window.open(`https://opensea.io/collection/people-of-eden`)
              }}
              trackingid={'minted-nft-view-on-opensea'} trackingParams={{position: 'mint-modal'}}
              className='primary-button primary-button-text' disabled={data.isLoading}>
              View on opensea
            </Button>
          </div>
          {/*<div className='flex-space-between share-on-twitter'>
            <Button
              onClick={() => window.open(`https://twitter.com/intent/tweet?text=${msg + '&url=' + data.value?.image}`)}
              trackingid={'minted-nft-share-on-twitter'} trackingParams={{position: 'mint-modal'}}
              className='primary-button primary-button-text' disabled={data.isLoading}>
              Share on twitter
            </Button>
          </div>*/}
        </div>
      </div>
    </CustomModal>
  )
}

export default SuccessfulMintModal;
