import {getDefaultWallets, RainbowKitProvider,} from "@rainbow-me/rainbowkit";
import '@rainbow-me/rainbowkit/dist/index.css'
import Aos from "aos";
import {SnackbarProvider} from "notistack";
import React, {useEffect} from 'react';
import {Provider} from "react-redux";
import {HashRouter as Router} from 'react-router-dom';
import {configureChains, createClient, WagmiConfig} from "wagmi";
import {alchemyProvider} from 'wagmi/providers/alchemy';
import {publicProvider} from "wagmi/providers/public";
import ChainUpdater from "./components/ChainUpdater";
import Popups from "./components/Popups";
import Tracking from "./components/Tracking";
import {ConfigChains} from './config';
import ModalsProvider from './context/Modals';
import {ProtocolProvider} from './context/ProtocolProvider';
import useCore from "./hooks/useCore";
import './index.css';

// import Popups from "./components/Popups";
import Navigation from './Navigation';

import store from "./state";
import {useGetUpdateActiveChainId} from "./state/chains/hooks";
import Updaters from "./state/Updaters";
import {rainbowKitTheme} from "./utils/rainbowKitTheme";

const isProduction = process.env.REACT_APP_ISPROD === 'true'

//@ts-ignore
const Providers = ({children}) => {
  return (

    <Provider store={store}>
      {
        //@ts-ignore
      }<RainbowProvider>
      {
        //@ts-ignore
      }<WalletProvider>
      {children}
    </WalletProvider>
    </RainbowProvider>
    </Provider>

  );
};
//@ts-ignore
const WalletProvider = ({children}) => {
  return (
    <>
      <Updaters/>
      <ProtocolProvider>
        {
          //@ts-ignore
        }<AppContent>{children}</AppContent>
      </ProtocolProvider>
    </>
  );
};
//@ts-ignore
const RainbowProvider = ({children}) => {
  const {chains, provider} = configureChains(
    ConfigChains,
    [
      alchemyProvider({apiKey: 'HY3urTDGBnhgqGXmCsjPEzyiVSY3NLh8'}),
      publicProvider()]
  );

  const {connectors} = getDefaultWallets({
    appName: "MahaDAO NFT",
    chains,
  });

  const wagmiClient = createClient({
    autoConnect: true,
    connectors,
    provider,
  });

  return (
    <WagmiConfig client={wagmiClient}>
      <RainbowKitProvider
        chains={chains}
        showRecentTransactions={true}
        theme={rainbowKitTheme}
      >
        {children}
      </RainbowKitProvider>
    </WagmiConfig>
  );
};

//@ts-ignore
const AppContent = ({children}) => {
  const core = useCore();
  const setChainId = useGetUpdateActiveChainId();

  useEffect(() => {
    if (window.ethereum)
      // @ts-ignore
      window.ethereum.on('chainChanged', (chainId) => {
        if (chainId == '0x1')
          setChainId(1);
        else if (chainId == '0x5')
          setChainId(5);
      });
  }, [window.ethereum]);

  if (!core) return <div/>;

  return (
    <ModalsProvider>
      <SnackbarProvider
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        maxSnack={2}
        autoHideDuration={2500}
      >
        <>
          <Popups/>
          {children}
        </>
      </SnackbarProvider>
    </ModalsProvider>
  );
};

export let isMobileGlobal = false;

const App = () => {
  const makeUnPassive = (ev: any) => {
    ev.preventDefault();
  };

  useEffect(() => {
    document.body.addEventListener('touchmove', makeUnPassive, {passive: true});
    return () => document.body.removeEventListener('touchmove', makeUnPassive);
  }, []);


  useEffect(() => {
    Aos.init();
  }, [])

  return (
    //@ts-ignore
    <Providers>
      <Router>
        <ChainUpdater/>
        <Navigation/>
        <Tracking/>
      </Router>
    </Providers>
  );
};

export default App;

if (isProduction) console.log = function () { };
