import React, {useState} from "react";
import {useMediaQuery} from "react-responsive";
import {NavLink} from "react-router-dom";
import Anchor from "../../../components/Anchor";
import Div from "../../../components/Div";
import IconLoader from "../../../components/IconLoader";

// import maharajaImg from '../../../images/vectors/maharaja-menu.png';
import maharajaImg from '../../../images/gifs/footer-gif.gif';
import discord from '../../../images/icons/discord.svg';
import openSea from '../../../images/icons/OpenSea.svg';
import star from '../../../images/icons/star-black.svg';
import FooterModal from "../modal/FooterModal";

import '../style/Footer.css';

interface IProps {
  showFooterWithoutAnimation?: boolean
  bg?: string
}

const Footer = (props: IProps) => {
  const {bg = '#FFEDE1', showFooterWithoutAnimation = false} = props;
  const isMobile = useMediaQuery({query: '(max-width: 768px)'});
  const [openFooterNav, setOpenFooterModal] = useState<boolean>(false);

  const [animationClass, setAnimationClass] = React.useState(showFooterWithoutAnimation ? '' : 'display-none')

  if (!props.showFooterWithoutAnimation) {
    window.addEventListener('scroll', handleScroll);
  }

  function handleScroll() {
    const lastScrollTop = 0;
    let scrollPosition = window.pageYOffset || window.scrollY || document.body.scrollTop || document.documentElement.scrollTop;

    if (scrollPosition > 400)
      setAnimationClass('animate__fadeInUp')
    else {
      if (scrollPosition > lastScrollTop) {
      } else {
        setAnimationClass('animate__fadeOutDown')
      }
    }
  }

  const MobileNav = () => {
    return (
      <div className={'header-wrapper'}>
        <Div className={'menu-img'} trackingid={'brand-identity'} trackingParams={{position: 'footer'}}>
          <NavLink to={'/'}>
            <img src={maharajaImg} width={'68px'} height={'76px'} className={'.'}/>
          </NavLink>
        </Div>
        <div className={'header'}>
          PEOPLE OF EDEN
        </div>
        <IconLoader className="mr10" iconName={'BentoMenu'} onClick={() => {
          setOpenFooterModal(true)
        }}/>
      </div>
    )
  }

  const DesktopNav = () => {
    return (
      <div className={'header-wrapper'}>
        <Div className={'header pointer'} trackingid={'brand-name'} trackingParams={{position: 'footer'}}
             onClick={() => window.open('/', '_self')}>
          PEOPLE OF EDEN
        </Div>
        <Div className={'menu-img'} trackingid={'brand-identity'} trackingParams={{position: 'footer'}}>
          <NavLink to={'/'}>
            <img src={maharajaImg} className={'.'}/>
          </NavLink>
        </Div>
        <div className={'menu'}>
          <Anchor className={'menu-items'} href={'/#/utilities'} trackingid={'utilities'}
                  trackingParams={{position: 'footer'}}>Utilities</Anchor>
          <span className={'star-divider'}><img src={star} alt={'.'}/></span>
          <Anchor className={'menu-items'} href={'/#/gallery'} trackingid={'explore-nft'}
                  trackingParams={{position: 'footer'}}>Explore NFTs</Anchor>
          <span className={'star-divider'}><img src={star} alt={'.'}/></span>
          <Anchor className={'menu-items'} href={'/#/faq'} trackingid={'faq'}
                  trackingParams={{position: 'footer'}}>FAQ</Anchor>
          <span className={'star-divider'}><img src={star} alt={'.'}/></span>
          <Anchor className={'menu-items'} href={'/#/account'} trackingid={'profile'}
                  trackingParams={{position: 'footer'}}>Profile</Anchor>
          <span className={'star-divider'}><img src={star} alt={'.'}/></span>
          <Anchor trackingid="join-discord" className={'social-media menu-items'} trackingParams={{position: 'footer'}}
                  href={'https://discord.com/invite/mahadao'}><img className="mr10" src={discord}
                                                                   alt={'Discord'}/></Anchor>
          {/*<span className={'star-divider'}><img src={star} alt={'.'}/></span>
          <Anchor trackingid="view-on-opensea" className={'social-media menu-items'} trackingParams={{position: 'footer'}}
                  href={'https://opensea.com/collection/people-of-eden'}><img className="mr10" src={openSea}
                                                                   alt={'opensea'} width={28}/></Anchor>*/}
        </div>
      </div>
    )
  }

  return (
    <div id={'Footer'} style={{backgroundColor: `${bg}`}} className={`animate__animated ${animationClass}`}>
      <div className={'main'}>
        {
          isMobile
            ? <MobileNav/>
            : <DesktopNav/>
        }
      </div>
      <FooterModal openModal={openFooterNav} onDismiss={() => setOpenFooterModal(false)}/>
    </div>
  )
}

export default Footer;
