import { Grid } from "@material-ui/core";
import React from "react";

export type IGender = 'Male' | 'Female' | '';
export type ISkin = 'One' | 'Two' | 'Three' | '';

interface IProps {
  gender: IGender;
  skin: ISkin;
  setGender: (data: IGender) => void;
  setSkin: (data: ISkin) => void;
}

const SkinGenderSelection = (props: IProps) => {

  const handleGenderChange = (value: IGender) => {
    props.setGender(value);
  }

  const handleSkinChange = (value: ISkin) => {
    props.setSkin(value);
  }

  return (
    <Grid container spacing={2} className={'mb24'}>
      <Grid item xs={5}>
        <div className='modal-content-heading mb8'>Gender *</div>
        <div className='flex-start-center'>
          <div
            onClick={() => handleGenderChange('Male')}
            className={`gender-option mr8 ${props.gender === 'Male' ? 'gender-option-selected' : ''}`}>
            <div
              className='modal-content-heading'
              style={{ color: '#0F0F13' }}>
              Male
            </div>
          </div>
          <div
            onClick={() => handleGenderChange('Female')}
            className={`gender-option ${props.gender === 'Female' ? 'gender-option-selected' : ''}`}>
            {/*<img src={Female_Icon} alt={'Female_Icon'} className={'mr8'}/>*/}
            <div
              className='modal-content-heading'
              style={{ color: '#0F0F13' }}>
              Female
            </div>
          </div>
        </div>
      </Grid>
      <Grid item xs={7}>
        <div className='modal-content-heading mb8'>Skin Color *</div>
        <div className='flex-start-center skin-option-container'>
          <div
            onClick={() => handleSkinChange('One')}
            className={`skin-option mr8 ${props.skin === 'One' ? 'skin-option-selected' : ''}`}>
            <div
              className='modal-content-heading text-center'
              style={{ color: '#fff' }}>One
            </div>
          </div>
          <div
            onClick={() => handleSkinChange('Two')}
            className={`skin-option mr8 ${props.skin === 'Two' ? 'skin-option-selected' : ''}`}>
            <div
              className='modal-content-heading text-center'
              style={{ color: '#fff' }}>Two
            </div>
          </div>
          <div
            onClick={() => handleSkinChange('Three')}
            className={`skin-option ${props.skin === 'Three' ? 'skin-option-selected' : ''}`}>
            <div
              className='modal-content-heading text-center'
              style={{ color: '#fff' }}>Three
            </div>
          </div>
        </div>
      </Grid>
    </Grid>
  )
}

export default SkinGenderSelection;
