import {BigNumber} from 'ethers'
import {useState} from 'react'


import {useMediaQuery} from 'react-responsive';
import CustomModal from "../../components/Modal";
import "../../customCss/Custom-Slider.css"
import ModalClose from "../../images/icons/ModalClose.svg";
import faq_divider_mob from "../../images/vectors/faq_divider_mob.svg";
import modal_divider from "../../images/vectors/modal_divider.svg";
import EvolveWithMaha from "../../modules/SingleNFT/modals/component/EvolveWithMaha";
import {ITxStatus} from "../../utils/interface";
import MintingInProgressModal from "../MintModal/MintingInProgressModal";
import "../MintModal/MintModal.css"
import SuccessfulMintModal from "../MintModal/SuccessfulMintModal";

interface Iprops {
  nftId: BigNumber;
  modalOpen: boolean
  onModalClose: () => void
}

const EvolveNFTModal = (props: Iprops) => {
  const {
    modalOpen,
    onModalClose,
    nftId
  } = props
  const isMobile = useMediaQuery({query: '(max-width: 768px)'})
  const [txStatus, setTxStatus] = useState<ITxStatus>('initial');


  return (
    <div>
      <MintingInProgressModal open={txStatus === 'in-progress'} handleClose={() => setTxStatus('')}/>
      <SuccessfulMintModal nftid={nftId.toString()} open={txStatus === 'successful'} handleClose={() => {
        setTxStatus('');
        onModalClose();
      }}/>
      <CustomModal open={modalOpen && txStatus === 'initial'} onClose={onModalClose}>
        <div>
          <div className='mint-modal-heading'>
            <div>Evolve Citizen</div>
            <div className='pointer' onClick={onModalClose}><img src={ModalClose} alt={'ModalClose'}/></div>
          </div>
          <div className='text-center mb16'>
            {
              isMobile ?
                <img src={faq_divider_mob} alt={'faq_divider_mob'} className={'responsiveImg'}/> :
                <img src={modal_divider} alt={'modal_divider'} className={'responsiveImg'}/>
            }
          </div>
          <div>
            <EvolveWithMaha nftId={nftId} txStatus={txStatus} setTxStatus={(value) => setTxStatus(value)}/>
          </div>
        </div>
      </CustomModal>
    </div>
  )
}

export default EvolveNFTModal
