import {BigNumber} from "ethers";
import {useCallback} from "react";
import {IGender, ISkin} from "../../../Modals/MintModal/SkinGenderSelection";
import {useAddPopup} from "../../../state/application/hooks";
import {useGetActiveChainId} from "../../../state/chains/hooks";
import {useTransactionAdder} from "../../../state/transactions/hooks";
import {getDisplayBalance} from "../../../utils/formatBalance";
import formatErrorMessage from "../../../utils/formatErrorMessage";

import useCore from "../../useCore";
import {genderMapping, skinMapping} from "./useLockMAHA";

const useLockWithETH = (ethVal: BigNumber, mahaAmount: BigNumber, gender: IGender, skin: ISkin) => {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();
  const skinNumber = skinMapping[skin] || 0;
  const genderNumber = genderMapping[gender] || 0;

  return useCallback(
    async (onTxInitiated: () => void, onSuccess: (id: string) => void, onFailure: (e: string) => void): Promise<void> => {
      try {
        const contract = core.getEthMahaxLockerContract(chainId);
        onTxInitiated();
        const response = await contract.createLockWithMetadataWithETH(
          ethVal,
          mahaAmount,
          true,
          [skinNumber, genderNumber, 0, 0, 0], {value: ethVal});

        addTransaction(response, {
          summary: `Lock ${Number(getDisplayBalance(ethVal)).toLocaleString("en-US", {minimumFractionDigits: 3})} ETH.`,
        });

        const tx = await response.wait();
        const getLogWithNFTId = tx.logs[4].topics[3];

        if (tx?.status === 1) onSuccess(parseInt(getLogWithNFTId).toString());
        if (tx?.status !== 1) onFailure('');
      } catch (e: any) {
        onFailure(formatErrorMessage(e?.data?.message || e?.message));
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [core, chainId, ethVal, mahaAmount, skinNumber, genderNumber, addTransaction, addPopup]
  );
};

export default useLockWithETH;
