// import {useWallet} from "use-wallet";
import {BigNumber} from "ethers";
import {useCallback, useEffect, useState} from "react";
import {useAccount} from "wagmi";
import {useGetActiveChainId} from "../../../state/chains/hooks";

import {LOADING_DEFAULT_BASIC_BOOLEAN_STATE, NON_LOADING_DEFAULT_BASIC_BOOLEAN_STATE,} from "../../../utils/constants";
import {BasicBooleanState} from "../../../utils/interface";
import useCore from "../../useCore";

const useHasChoosenTheTraits = (nftId: BigNumber) => {
  const [value, setValue] = useState<BasicBooleanState>(
    LOADING_DEFAULT_BASIC_BOOLEAN_STATE
  );

  const core = useCore();
  const {address: account} = useAccount()

  const chainId = useGetActiveChainId();
  const contract = core.getMetadataContract(chainId);

  const fetchData = useCallback(async () => {
    if (!account) {
      setValue(NON_LOADING_DEFAULT_BASIC_BOOLEAN_STATE);
      return;
    }

    const number: BigNumber = await contract.historyCount(nftId);

    const bool = !number.eq(0);

    setValue({
      isLoading: false,
      value: bool,
    })

  }, [account, contract, nftId]);

  useEffect(() => {
    if (core) {
      fetchData().catch((err) => {
        setValue(NON_LOADING_DEFAULT_BASIC_BOOLEAN_STATE);
        console.error(`Failed to fetch hasChoosenTheTraits: ${err.stack}`);
      });
    }
  }, [setValue, core, account, fetchData]);

  return value;
};

export default useHasChoosenTheTraits;
