import {useCallback, useEffect, useState} from "react"
import {getCollateralPrices} from "../../utils/getCollateralPrices"


export const useGetMahaToEth = (mahaVal: number) => {
  const [res, setRes] = useState(0)

  const fetchRes = useCallback(async () => {
    const prices = await getCollateralPrices()
    setRes(prices.MahaToEth * mahaVal)
  }, [mahaVal])

  useEffect(() => {
    fetchRes()
      .catch((err) => console.error('useGetMahaToEth', err))
  }, [fetchRes, mahaVal])

  return res
}
