import {Grid} from "@material-ui/core";
import React, {useMemo, useState} from "react";
import Loader from "react-spinners/BeatLoader";
import {useAccount} from "wagmi";
import Button from "../../components/Button";
import useLockWithETH from "../../hooks/callbacks/Minting/useLockWithETH";
import useGetMAHAFromETH from "../../hooks/state/Minting/useGetMAHAFromETH";
import {useGetMahaToEth} from "../../hooks/state/useGetMahaToEth";
import useGetNativeTokenBalance from "../../hooks/state/useGetNativeTokenBalance";
import useCore from "../../hooks/useCore";
import useTokenBalance from "../../hooks/useTokenBalance";
import {formatToBN, getDisplayBalance} from "../../utils/formatBalance";
import {useGetChainId} from "../../utils/NetworksCustomHooks";
import ETHSlider from "./EthSlider";
import SkinGenderSelection, {IGender, ISkin} from "./SkinGenderSelection";

export type ITxStatus = 'initial' | 'not-initiated' | 'in-progress' | 'successful' | 'not-successful' | '';

interface IProps {
  txStatus: ITxStatus;
  setTxStatus: (status: ITxStatus, nftId?: string, msg?: string) => void;
}

const MintWithETH = (props: IProps) => {
  const {isConnected} = useAccount()
  const core = useCore();
  const chainId = useGetChainId();
  const tokenETHBal = useGetNativeTokenBalance();
  const minETH = useGetMahaToEth(100);
  const [sliderVal, setSliderVal] = useState<number>(minETH);
  const MAHAErc = core._tokens[chainId]['MAHA'];
  const tokenMahaBal = useTokenBalance(MAHAErc);
  const [gender, setGender] = useState<IGender>('');
  const [skin, setSkin] = useState<ISkin>('');

  const mahaReceived = useGetMAHAFromETH(sliderVal);
  const ethBalanceToMaha = useGetMAHAFromETH(Number(getDisplayBalance(tokenETHBal.value)));

  const isEthSatisfied = useMemo(() => {
    if (tokenETHBal.isLoading || ethBalanceToMaha.isLoading) return false;
    else if (ethBalanceToMaha.value.gte(formatToBN(100))) return true;
    return false
  }, [ethBalanceToMaha.isLoading, ethBalanceToMaha.value, tokenETHBal.isLoading])

  /* const isMinimumEthSupplied = useMemo(() => {
     if (mahaReceived.isLoading) return false;
     else if (mahaReceived.value.gte(formatToBN(100))) return true;
     else return false
   }, [mahaReceived.isLoading, mahaReceived.value])*/

  const lockMAHAAction = useLockWithETH(
    formatToBN(sliderVal, 18),
    mahaReceived.value,
    gender,
    skin
  )

  const handleMint = () => {
    lockMAHAAction(() => {
        props.setTxStatus('in-progress')
      },
      (nftID: string) => {
        props.setTxStatus('successful', nftID)
      },
      (msg: string) => {
        props.setTxStatus('not-successful', undefined, msg)
      }
    ).then(() => {
      console.log("inside then")
    })
  }

  const isMintDisabled = useMemo(() => {
    return skin === '' || gender === '' || mahaReceived.value.lt(formatToBN(100))
  }, [gender, mahaReceived.value, skin])

  if (tokenETHBal.isLoading) {
    return (
      <div className={'mint-modal-loading'}>
        <Loader size={8} color={'black'} className={'text-center m-b-32'}/>
        <div className={'enough-maha-error'}>We are checking your balances to see if you can mint your
          citizen.
        </div>
      </div>
    )
  }

  if (!isEthSatisfied) {
    if (tokenMahaBal.isLoading) {
      return (
        <div>
          <Loader size={8} color={'black'} className={'text-center m-b-32'}/>
          <div className={'enough-maha-error'}>We are checking your balances to see if you can mint your
            citizen.
          </div>
        </div>
      )
    } else {
      if (!tokenMahaBal.isLoading && tokenMahaBal.value.gte(formatToBN(100))) {
        return (
          <div className={'mint-modal-loading'}>
            <div className={'enough-maha-error'}>Hey! Looks like you dont have enough ETH to mint your citizenship. But
              you can get it with MAHA.
            </div>
          </div>
        )
      } else {
        return (
          <div className={'mint-modal-loading'}>
            <div className={'enough-maha-error'}>Looks like you dont have enough ETH or MAHA to mint a citizenship. But
              weve got your back! Mint your citizenship by getting MAHA from Kucoin or Uniswap.
            </div>
            <div className={'loading-cta w-100'}>
              <Grid container spacing={2}>
                <Grid item lg={6} md={6} sm={6} xs={6}>
                  <div className={'mint-nft-btn'}>
                    <Button trackingid={'buy-from-kucoin'}
                            className={'primary-button-text primary-button primary-modal-button-text'}
                            onClick={() => window.open('https://trade.kucoin.com/MAHA-USDT', '_blank')}>
                      Kucoin
                    </Button>
                  </div>
                </Grid>
                <Grid item lg={6} md={6} sm={6} xs={6}>
                  <div className={'mint-nft-btn'}>
                    <Button trackingid={'buy-from-uniswap'}
                            className={'primary-button-text primary-button primary-modal-button-text'}
                            onClick={() => window.open('https://app.uniswap.org/#/swap?outputCurrency=0x745407c86df8db893011912d3ab28e68b62e49b0', '_blank')}>
                      Uniswap
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </div>
          </div>)
      }
    }
  }

  return (
    <div>
      <ETHSlider sliderValue={sliderVal} setSliderValue={setSliderVal} tokenBalance={tokenETHBal}
                 mahaSliderValue={Number(getDisplayBalance(mahaReceived.value))}
                 mahaSliderLoading={mahaReceived.isLoading}/>
      <SkinGenderSelection gender={gender} skin={skin} setGender={setGender} setSkin={setSkin}/>
      {
        !tokenETHBal.isLoading && tokenETHBal.value.lt(formatToBN(sliderVal)) &&
          <div className='modal-content-heading mb12' style={{color: 'red'}}>Your minimum balance should be 100
              MAHA.</div>
      }
      {
        mahaReceived.value.lt(formatToBN(100)) &&
          <p className={'error-msg text-center'}>* Minimum MAHA amount for a lock is not meet. Try supplying more ETH
              (at least 100 MAHA worth).</p>
      }
      {
        (gender === '' || skin === '') &&
          <p className={'error-msg text-center'}>* You need to choose a gender and skin color</p>
      }
      <p className={'tnc-msg text-center'}>* You will spend {sliderVal.toFixed(2)} ETH which gets swapped
        for {Number(getDisplayBalance(mahaReceived.value)).toFixed(2)} MAHA and the same gets locked for 4 years in an
        NFT</p>
      {
        (gender !== '' && skin !== '') && (
          <Button type="button" trackingid={'mint-with-eth'}
                  trackingParams={{value: Number(sliderVal), gender: gender, skin: skin}}
                  className='primary-button primary-modal-button-text' onClick={handleMint}
                  disabled={isMintDisabled}>
            Mint NFT
          </Button>
        )
      }
      {/*<div className={'flex-center'}>
        <div className='modal-bottom-val'>* Your
          <span className='modal-bottom-val-title'> {sliderVal} ETH </span> would be swapped for MAHA and the same MAHA would be locked for 4 years, and you will
          receive
          <span className='modal-bottom-val-title'> {mahaXReceived} MAHAX </span> which can be used in&nbsp;
          <Link target={'_blank'} className='modal-bottom-val-title'
                to={'https://gov.mahadao.com/#/locker'}>governance</Link>.
        </div>
      </div>*/}
    </div>
  )
}

export default MintWithETH;
