import {ethers} from "ethers/lib.esm";
import React, {useMemo} from "react";
import {useParams} from "react-router-dom";
import Button from "../../components/Button";
import useGetNumberOfNFTs from '../../hooks/state/Minting/useGetNumberOfNFTs';

import divider from "../../images/vectors/faq-divider.png";
import Footer from "../Home/sections/Footer";
import Collection from "./components/Collection";

import ProfileSection from "./components/ProfileSection";
import './styles/index.css';

interface IProps {
  myAddress?: string;
}

const SingleNft = (props: IProps) => {
  const {myAddress} = props;
  const {id} = useParams();

  const walletAddress = useMemo(() => {
    return myAddress || id || ''
  }, [myAddress, id]);

  const isValidAddress = useMemo(() => {
    return ethers.utils.isAddress(walletAddress);
  }, [walletAddress])

  const noOfNfts = useGetNumberOfNFTs(walletAddress);

  if (!isValidAddress) {
    return (
      <div id={'Profile'}>
        <div className={'container'}>
          <div className={'error-msg'}>
            Oops! user not fond
          </div>
          <div>
            <Button className={'primary-button primary-button-text'} trackingid={'explore-nft'}
                    trackingParams={{position: 'profile-page-PNF'}}>
              Explore NFT
            </Button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div id={'Profile'}>
      <div className={'container'}>
        <ProfileSection urlAddress={walletAddress} nftCount={noOfNfts}/>
        <img src={divider} className={'divider'} alt={'divider'}/>
        <Collection urlAddress={walletAddress} nftCount={noOfNfts.value.toNumber()}/>
      </div>
      <Footer showFooterWithoutAnimation={true} bg={'#FFEDE1'}/>
    </div>
  )
}

export default SingleNft;
