import {Card, FormControl, Grid, InputAdornment, MenuItem, OutlinedInput, Select} from "@material-ui/core"
import Pagination from '@mui/material/Pagination';
import {ConnectButton} from "@rainbow-me/rainbowkit";
import {useEffect, useMemo, useState} from "react"
import {Oval} from "react-loader-spinner";
import {useMediaQuery} from "react-responsive"
import {useLocation, useNavigate} from "react-router-dom";
import {ITraits} from "../../hooks/state/Minting/useGetNFTDetails";
import search_icon from "../../images/icons/search_icon.svg"
import faqdivider from "../../images/vectors/faq-divider.png"
import gallery_divider_mob from "../../images/vectors/gallery_divider_mob.svg"
import callApi from "../../utils/apiCaller";
import Footer from "../Home/sections/Footer"
import NftWithID from "./components/NftWithID";
import "./style/galleryStyle.css"

export interface IApiData {
  count: number;
  page: number;
  results: IGalleryItem[];
}

export interface IGalleryItem {
  "_id": string;
  "nftId": number;
  "mahaxAmount": number;
  "name": string;
  imageUrl: string;
  "attributes": ITraits[];
}

export type sortingKeys = 'id' | 'id-reverse' | 'maha' | 'maha-reverse';

const Gallery = () => {
  const isTab = useMediaQuery({query: '(max-width: 768px)'})

  const [sortBy, setSortby] = useState<sortingKeys>('id')
  const [searchText, setSearchText] = useState<string>('')
  const [nftArr, setNftArr] = useState<IGalleryItem[]>([]);
  const [page, setPage] = useState<number>(1);
  const [loading, setLoading] = useState<boolean>(false);
  const [noOfNFTs, setNoOfNFTs] = useState<number>(1200);
  const nftInSinglePage = 48;
  const { search } = useLocation();
  let history = useNavigate();

  useEffect(() => {
    window.scrollTo({top: 0})
  }, [])

  const getNfts = () => {
    setLoading(true)
    var sortingKeys = sortBy.split('-');
    const initial = `?page=${page - 1}&count=${nftInSinglePage}`;
    const extendedUrl = `sort=${sortingKeys[0]}${sortingKeys[1] === 'reverse'? '&reverse=true': ''}`;
    const finalUrl = `${initial}&${extendedUrl}`;
    // if (search !== finalUrl) history(`/gallery${finalUrl}`);
    callApi(`query-nfts${finalUrl}`)
      .then((res: IApiData) => {
        setLoading(false)
        setNftArr(res.results);
        setNoOfNFTs(res.count);
      })
      .catch((err) => {
        setLoading(false)
        setNftArr([])
        setNoOfNFTs(0);
        console.log('getNfts err', err)
      })
  }

  useEffect(() => {
    getNfts()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page, nftInSinglePage, sortBy, search])

  const handleSortby = (event: React.ChangeEvent<{ value: unknown }>) => {
    setSortby(event.target.value as sortingKeys);
  };

  const handleSearchText = (event: React.ChangeEvent<{ value: unknown }>) => {
    setSearchText(event.target.value as string);
  };

  const filteredArray = useMemo(() => {
    if (searchText !== '') return nftArr
      .filter(value => value.nftId.toString().includes(searchText))
    else return nftArr;
  }, [searchText, nftArr]);

  const noOfPages = useMemo(() => {
    return Math.ceil(noOfNFTs / nftInSinglePage);
  }, [noOfNFTs]);

  return (
    <div id="gallery">
      <div>
        <div className="heading-section">
          <div className="heading-6" style={{textAlign: isTab ? "center" : "left", marginBottom: isTab ? "" : ""}}>EDEN
            CITIZEN
          </div>
          <ConnectButton chainStatus={'none'}/>
          {/*<div className={"heading-btn pointer"}>
            <img className="mr10" src={ViewCollectionIcon} alt={'ViewCollectionIcon'}/>
            <Div trackingid="view-collection" className="heading-btn-text"
                 onClick={() => window.open(`/#/profile/${address}`, '_self')}>View collection</Div>
          </div>*/}
        </div>
        <div className="divider mb24">
          {
            isTab ?
              <img src={gallery_divider_mob} alt={'gallery_divider_mob'} className={"responsiveImg"}/> :
              <img src={faqdivider} className={"responsiveImg"}/>
          }
        </div>
        <div className="gallery-filter-container">
          {/* {!isTab && <FilterCard />}  */}
          <Card style={{width: !isTab ? '100%' : 'auto'}} className="searchbar">
            <Grid container alignItems={'center'} className="searchbar">
              <Grid item xs={12} md={6} className={'nft-count'}>
                Citizens
              </Grid>
              <Grid item xs={12} md={3} style={{marginBottom: isTab ? '12px' : '0px'}}>
                <Grid container alignItems={'center'}>
                  <Grid item xs={6} md={3}>
                    {/*{
                      isTab ?
                        <FilterSidebar/> :
                        <div/>
                    }*/}
                  </Grid>
                  <Grid item xs={6} md={9}>
                    <FormControl fullWidth={true} variant="outlined"
                                 style={{width: isTab ? '100%' : '90%', marginRight: !isTab ? '12px' : '0px'}}>
                      <Select
                        labelId="demo-simple-select-outlined-label"
                        id="demo-simple-select-outlined"
                        value={sortBy}
                        onChange={handleSortby}
                      >
                        <MenuItem value={'id'}>Id: Ascending</MenuItem>
                        <MenuItem value={'id-reverse'}>Id: Descending</MenuItem>
                        <MenuItem value={'maha'}>MAHAX: Ascending</MenuItem>
                        <MenuItem value={'maha-reverse'}>MAHAX: Descending</MenuItem>
                      </Select>
                    </FormControl>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12} md={3}>
                <FormControl fullWidth variant="outlined">
                  <OutlinedInput
                    id="outlined-adornment-amount"
                    value={searchText}
                    onChange={handleSearchText}
                    startAdornment={
                      <InputAdornment position="start">
                        <img src={search_icon} alt={'search_icon'}/>
                      </InputAdornment>}
                    placeholder={'Search by Citizen ID in this page'}
                  />
                </FormControl>
              </Grid>
            </Grid>
            {loading ?
              <div className="flex-center">
                <Oval height={80}
                      width={80}
                      color="#2e4d6e"
                      visible={true}
                      ariaLabel='oval-loading'
                      secondaryColor="#2e4d6e"
                      strokeWidth={2}
                      strokeWidthSecondary={2}/>
              </div> :
              <Grid container spacing={2}>
                {
                  filteredArray.map((nft, i) => <NftWithID key={i} nft={nft}/>)
                }
              </Grid>
            }
            <div className="flex-end-center">
              <Pagination
                count={noOfPages}
                onChange={(data, value) => setPage(value)}
                defaultValue={page}
              />
            </div>
          </Card>

        </div>
      </div>
      <div>
        <Footer bg={"#FFEDE1"}/>
      </div>
    </div>
  )
}

export default Gallery
