import React from "react";
import {useMediaQuery} from "react-responsive";

import CustomModal from "../../components/Modal";

// import MintingNFT from "../../images/gifs/Minting.gif";
import MintingNFT from "../../images/gifs/Minting-1.gif";
import ModalClose from "../../images/icons/ModalClose.svg";
import faq_divider_mob from "../../images/vectors/faq_divider_mob.svg";
import modal_divider from "../../images/vectors/modal_divider.svg";

interface IProps {
  open: boolean;
  handleClose: () => void;
}

const MintingInProgressModal = (props: IProps) => {
  const isMobile = useMediaQuery({query: '(max-width: 768px)'})

  return (
    <CustomModal open={props.open} onClose={props.handleClose}>
      <div>
        <div className='mint-modal-heading'>
          <div>Evolving your citizen</div>
          <div className='pointer' onClick={props.handleClose}><img src={ModalClose} alt={'ModalClose'}/></div>
        </div>
        <div className='text-center mb16'>
          {
            isMobile ?
              <img src={faq_divider_mob} alt={'faq_divider_mob'} className={'responsiveImg'}/> :
              <img src={modal_divider} alt={'modal_divider'} className={'responsiveImg'}/>
          }
        </div>
        <div className='nft-revealed-image in-progress-nft'>
          <img className='mb16' src={MintingNFT} alt="gif"/>
        </div>
        <p className={'text-center'}>Take a deep breath your citizen is evolving</p>
      </div>
    </CustomModal>
  )
}

export default MintingInProgressModal;
