import {BigNumber} from "ethers";
import React, {useEffect, useState} from "react";
import {Oval} from 'react-loader-spinner';
import {useParams} from "react-router-dom";
import useGetNFTDetails from "../../hooks/state/Minting/useGetNFTDetails";
import useHasChoosenTheTraits from "../../hooks/state/Minting/useHasChoosenTheTraits";

import divider from "../../images/vectors/faq-divider.png";
import EvolveNFTModal from "../../Modals/EvolveNFT";
import Footer from "../Home/sections/Footer";

import Header from "./components/Header";
import NFTDetailsSection from "./components/NFTDetailsSection";


import './style/index.css';

const SingleNFT = () => {
  const {id} = useParams();
  const nftDetail = useGetNFTDetails(Number(id));
  const [openEvolveModal, setOpenEvolveModal] = useState<boolean>(false);

  useEffect(() => {
    window.scrollTo({top: 0})
  }, [])

  return (
    <div id={'single-nft'}>
      <div className={'container'}>
        <Header nftDetail={id}/>
        <img src={divider} className={'divider'} alt={'divider'}/>
        {nftDetail.isLoading || nftDetail.value === null || nftDetail.value.id === null ?
          <div className="flex-center">
            <Oval height={80}
                  width={80}
                  color="#2e4d6e"
                  visible={true}
                  ariaLabel='oval-loading'
                  secondaryColor="#2e4d6e"
                  strokeWidth={2}
                  strokeWidthSecondary={2}/>
          </div> :
          <NFTDetailsSection nftDetail={nftDetail.value} onEvolvePress={() => setOpenEvolveModal(true)}/>
        }
        {/*{nftDetail.value !== null &&
            <EvolveNFTModal
                modalOpen={openEvolveModal}
                nftId={nftDetail?.value?.id}
                onModalClose={() => setOpenEvolveModal(false)}/>
        }*/}
      </div>
      <Footer showFooterWithoutAnimation={true}/>
    </div>
  )
}

export default SingleNFT;
