import {Grid} from "@material-ui/core";
import React, {useMemo, useState} from "react";
import Loader from 'react-spinners/BeatLoader';
import Button from "../../components/Button";
import useLockMAHA from "../../hooks/callbacks/Minting/useLockMAHA";
import useApprove, {ApprovalState} from "../../hooks/callbacks/useApprove";
import {useGetMahaToEth} from "../../hooks/state/useGetMahaToEth";
import useGetNativeTokenBalance from "../../hooks/state/useGetNativeTokenBalance";

import useCore from "../../hooks/useCore";
import useTokenBalance from "../../hooks/useTokenBalance";

import {formatToBN, getDisplayBalance} from "../../utils/formatBalance";
import {useGetChainId} from "../../utils/NetworksCustomHooks";

import MahaSlider from "./MahaSlider";
import SkinGenderSelection, {IGender, ISkin} from "./SkinGenderSelection";

export type ITxStatus = 'initial' | 'not-initiated' | 'in-progress' | 'successful' | 'not-successful' | '';

interface IProps {
  txStatus: ITxStatus;
  setTxStatus: (status: ITxStatus, nftId?: string, msg?: string) => void;
}

const MintWithMaha = (props: IProps) => {
  const core = useCore();
  const chainId = useGetChainId();
  const MAHAErc = core._tokens[chainId]['MAHA'];
  const tokenMahaBal = useTokenBalance(MAHAErc);
  const tokenETHBal = useGetNativeTokenBalance();
  const minETH = useGetMahaToEth(110);

  const [sliderVal, setSliderVal] = useState<number>(100);
  const [gender, setGender] = useState<IGender>('');
  const [skin, setSkin] = useState<ISkin>('');

  const [approveStatus, approve] = useApprove(
    core._tokens[chainId]['MAHA'],
    core.getEthMahaxLockerContract(chainId).address,
  );

  const isMahaApproved = useMemo(
    () => approveStatus === ApprovalState.APPROVED,
    [approveStatus]
  );
  const isMahaApproving = useMemo(
    () => approveStatus === ApprovalState.PENDING,
    [approveStatus]
  );

  const isEthSatisfied = useMemo(() => {
    if (tokenETHBal.isLoading) return false;
    if (minETH === 0) return false;
    else {
      const ethBalance = Number(getDisplayBalance(tokenETHBal.value, 18, 6));
      return ethBalance > minETH;
    }
  }, [minETH, tokenETHBal.isLoading, tokenETHBal.value])

  const lockMAHAAction = useLockMAHA(
    formatToBN(sliderVal),
    gender,
    skin
  )

  const handleMint = () => {
    lockMAHAAction(() => {
        props.setTxStatus('in-progress')
      },
      (nftID: string) => {
        props.setTxStatus('successful', nftID)
      },
      (msg: string) => {
        props.setTxStatus('not-successful', undefined, msg)
      }
    ).then(() => {
      console.log("inside then")
    })
  }

  const isMintDisabled = useMemo(() => {
    return skin === '' || !isMahaApproved || gender === '' || tokenMahaBal.value.lt(formatToBN(100))
  }, [gender, isMahaApproved, skin, tokenMahaBal.value])

  if (tokenMahaBal.isLoading) {
    return (
      <div className={'mint-modal-loading'}>
        <Loader size={8} color={'black'} className={'text-center m-b-32'}/>
        <div className={'enough-maha-error'}>We are checking your balance to see if you can mint your
          citizen.
        </div>
      </div>
    )
  }

  if (!tokenMahaBal.isLoading && tokenMahaBal.value.lt(formatToBN(100))) {
    if (tokenETHBal.isLoading) {
      return (
        <div>
          <Loader size={8} color={'black'} className={'text-center m-b-32'}/>
          <div className={'enough-maha-error'}>We are checking your balance to see if you can mint your
            citizen.
          </div>
        </div>
      )
    } else {
      if (isEthSatisfied) {
        return (
          <div className={'mint-modal-loading'}>
            <div className={'enough-maha-error'}>Looks like you dont have enough MAHA to mint your citizenship. But
              weve got your back! You can still try minting your citizenship with ETH by toggling the button.
            </div>
          </div>
        )
      } else {
        return (
          <div className={'mint-modal-loading'}>
            <div className={'enough-maha-error'}>You dont have enough MAHA to mint a citizenship. But no worries! You
              can get MAHA either from Kucoin or Uniswap.
            </div>
            <div className={'loading-cta w-100'}>
              <Grid container spacing={2}>
                <Grid item lg={6} md={6} sm={6} xs={6}>
                  <div className={'mint-nft-btn'}>
                    <Button trackingid={'buy-from-kucoin'}
                            className={'primary-button-text primary-button primary-modal-button-text'}
                            onClick={() => window.open('https://trade.kucoin.com/MAHA-USDT', '_blank')}>
                      Kucoin
                    </Button>
                  </div>
                </Grid>
                <Grid item lg={6} md={6} sm={6} xs={6}>
                  <div className={'mint-nft-btn'}>
                    <Button trackingid={'buy-from-uniswap'}
                            className={'primary-button-text primary-button primary-modal-button-text'}
                            onClick={() => window.open('https://app.uniswap.org/#/swap?outputCurrency=0x745407c86df8db893011912d3ab28e68b62e49b0', '_blank')}>
                      Uniswap
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </div>
          </div>)
      }
    }
  }

  return (
    <div>
      <MahaSlider sliderValue={sliderVal} setSliderValue={setSliderVal} tokenBalance={tokenMahaBal}/>
      <SkinGenderSelection gender={gender} skin={skin} setGender={setGender} setSkin={setSkin}/>
      {
        !tokenMahaBal.isLoading && tokenMahaBal.value.lt(formatToBN(100)) &&
          <p className={'error-msg text-center'}>* You need to have minimum 100 MAHA to mint an NFT</p>
      }
      {
        (gender === '' || skin === '') &&
          <p className={'error-msg text-center'}>* You need to choose a gender and skin color</p>
      }
      {
        (gender === '' || skin === '') &&
          <p className={'tnc-msg text-center'}>* You will spend {sliderVal.toFixed(2)} MAHA which gets locked for 4
              years in an NFT</p>
      }
      <div data-rk={"true"} className={'mint-nft-btn'}>
        {
          (gender !== '' && skin !== '') && (
            isMahaApproved ?
              <Button type="button" trackingid={'mint-with-maha'}
                      trackingParams={{value: Number(sliderVal), gender: gender, skin: skin}}
                      className='primary-button primary-modal-button-text' onClick={handleMint}
                      disabled={isMintDisabled}>
                Mint NFT
              </Button>
              :
              <Button type="button" trackingid={'approve-maha'} className='primary-button primary-modal-button-text'
                      onClick={approve}
                      disabled={isMahaApproving || Number(sliderVal) < 100}>
                {isMahaApproving ? 'Approving' : 'Approve'}
              </Button>
          )
        }
      </div>
      {/*<div className={'flex-center'}>
        <div className='modal-bottom-val'>* Your
          <span className='modal-bottom-val-title'> {sliderVal} MAHA </span> would be locked for 4 years, and you will
          receive
          <span className='modal-bottom-val-title'> {mahaXReceived} MAHAX </span> which can be used in&nbsp;
          <Link target={'_blank'} className='modal-bottom-val-title'
                to={'https://gov.mahadao.com/#/locker'}>governance</Link>.
        </div>
      </div>*/}
    </div>
  )
}

export default MintWithMaha;
