import {Grid} from "@material-ui/core"
import Div from "../../../components/Div"
import "../style/galleryStyle.css"
import {IGalleryItem} from "../index";
import placeholder from '../../../images/maharaja/placeholder.png';

interface IProps {
  nft: IGalleryItem;
}

const NftWithID = (props: IProps) => {
  const {nft} = props;
  return (
    <Grid item xs={12} sm={6} md={3} lg={2} id={'nft-with-id'} className="grow">
      <Div trackingid="nft-item" trackingParams={{id: nft.nftId.toString(), position: 'gallery-page'}}
           onClick={() => window.open(`/#/gallery/${nft.nftId}`, '_self')}>
        <div className={'nft-image-container'}>
          <img src={nft.imageUrl || placeholder} alt={`Eden ${nft.nftId}`} style={{width: '100%', height: '100%'}}/>
        </div>
        <div className={'nft-id'}>CITIZEN #{nft.nftId}</div>
        <div className={'maha-amount'}>{Number(nft.mahaxAmount).toFixed(2)} MAHAX</div>
      </Div>
    </Grid>
  )
}

export default NftWithID

