import {useCallback, useEffect, useState} from "react";
import {useGetActiveChainId} from "../../../state/chains/hooks";

import {LOADING_DEFAULT_BASIC_STATE, NON_LOADING_DEFAULT_BASIC_STATE,} from "../../../utils/constants";
import {formatToBN} from "../../../utils/formatBalance";
import {BasicState} from "../../../utils/interface";
import useCore from "../../useCore";

const useGetMAHAFromETH = (ethSupplyNumber: number) => {
  const [value, setValue] = useState<BasicState>(
    LOADING_DEFAULT_BASIC_STATE
  );

  const core = useCore();
  const chainId = useGetActiveChainId();
  const contract = core.getV3StakingQuote(chainId);

  const fetchData = useCallback(async () => {
    const ethSupply = formatToBN(ethSupplyNumber, 18);
    if (ethSupply.eq(0)) return setValue(NON_LOADING_DEFAULT_BASIC_STATE);
    setValue(LOADING_DEFAULT_BASIC_STATE);
    const response = await contract.callStatic.quoteExactInputSingle(
      core._config[chainId].wethAddress,
      core._config[chainId].mahaAddress,
      10000,
      ethSupply,
      0,
    )

    setValue({
      isLoading: false,
      value: response
    })

  }, [chainId, contract.callStatic, core._config, ethSupplyNumber]);

  useEffect(() => {
    if (core) {
      fetchData().catch((err) => {
        setValue(NON_LOADING_DEFAULT_BASIC_STATE);
        console.error(`Failed to fetch voting status: ${err.stack}`);
      });
    }
  }, [core, fetchData]);

  return value;
};

export default useGetMAHAFromETH;
