import React, {useMemo} from "react";
import Up_Arrow from "../../images/icons/Up_Arrow.svg";
import {formatTooltipVal, useGetSliderMarks} from "../../utils";
import {getDisplayBalance} from "../../utils/formatBalance";
import {BasicState} from "../../utils/interface";
import {
  PrettoSlider,
  Rarity,
  RarityColorsContainer, RarityColorsInnerContainer,
  SliderContainer,
  SliderLine,
  SliderPointer, SliderPointerText
} from "./styles";

interface IProps {
  sliderValue: number;
  setSliderValue: (value: number) => void;
  tokenBalance: BasicState;
}

function valuetext(value: number) {
  return `${value} %`;
}

const MahaSlider = (props: IProps) => {
  const marks = useGetSliderMarks('MAHA');
  const tokenMahaBal = props.tokenBalance;
  const tokenBalance = !tokenMahaBal.isLoading ? Number(getDisplayBalance(tokenMahaBal.value, 18, 6)) : 0;
  const handleChange = (event: any, newValue: number | number[]) => {
    const val = newValue as number
    props.setSliderValue(Math.max(val, 100));
  };

  const calcSliderPosition = useMemo(() => {
    const sliderWidth = document?.getElementById('rarity-chances')?.offsetWidth || 0;
    const twelvePart = sliderWidth / 12;
    if (props.sliderValue > 25000) {
      const preAdd = (twelvePart * 11);
      const constMultiplier = preAdd + (twelvePart / 2);
      return `${constMultiplier}px`;
    } else if (props.sliderValue > 5001) {
      const preAdd = (twelvePart * 9);
      const multiplier = (twelvePart * 2) / 25000;
      return `${preAdd + (multiplier * props.sliderValue)}px`
    } else if (props.sliderValue > 301) {
      const preAdd = (twelvePart * 5);
      const multiplier = (twelvePart * 4) / 5000;
      return `${preAdd + (multiplier * props.sliderValue)}px`
    } else {
      const multiplier = (twelvePart * 5) / 300;
      return `${multiplier * props.sliderValue}px`
    }
  }, [props.sliderValue])

  return (
    <div>
      <div className='modal-content-heading'>Supply</div>
      <div className='flex-space-between'>
        <div className='modal-content-values'> {props.sliderValue} {'MAHA'} &nbsp;
          <span className='modal-content-values' style={{color: 'rgba(0, 0, 0, 0.3)'}}></span>
        </div>
        <div className='modal-content-values'>Balance: {tokenBalance} {'MAHA'} &nbsp;
          <span className='modal-content-values' style={{color: 'rgba(0, 0, 0, 0.3)'}}></span>
        </div>
      </div>
      <PrettoSlider
        step={1}
        valueLabelDisplay="auto"
        aria-label="pretto slider"
        defaultValue={100}
        marks={marks}
        getAriaValueText={valuetext}
        value={Number(formatTooltipVal(props.sliderValue))}
        onChange={handleChange}
        min={0}
        max={Number(tokenBalance)}
      />
      <RarityColorsContainer id={'rarity-chances'}>
        <div className='modal-content-heading mb16'>Chances</div>
        <RarityColorsInnerContainer>
          <Rarity flex={5} bg={'#1D97DE'}>Common</Rarity>
          <Rarity flex={4} bg={'#EE8CB1'}>Rare</Rarity>
          <Rarity flex={2} bg={'#2E4D6E'}>Legendary</Rarity>
          <Rarity flex={1} bg={'#DF3E00'}>1/1s</Rarity>
          <SliderContainer style={{
            left: calcSliderPosition
          }}>
            <SliderPointer>
              {/*<SliderLine/>*/}
              <img src={Up_Arrow} alt={'Up_Arrow'} height={'16px'} width={'16px'}/>
              <SliderPointerText className='modal-content-heading'>Your Chances</SliderPointerText>
            </SliderPointer>
          </SliderContainer>
        </RarityColorsInnerContainer>

      </RarityColorsContainer>
    </div>
  )
}

export default MahaSlider;
