// import {useWallet} from "use-wallet";
import {useCallback, useEffect, useState} from "react";
import {useGetActiveChainId} from "../../../state/chains/hooks";
import {LOADING_DEFAULT_BASIC_STATE, NON_LOADING_DEFAULT_BASIC_STATE,} from "../../../utils/constants";
import {BasicState} from "../../../utils/interface";
import useCore from "../../useCore";

const useGetNumberOfNFTs = (address: string) => {
  const [value, setValue] = useState<BasicState>(
    LOADING_DEFAULT_BASIC_STATE
  );

  const core = useCore();
  // const {account} = useWallet();
  // const {address: account} = useAccount()

  const chainId = useGetActiveChainId();
  const contract = core.getVestingContract(chainId);

  const fetchData = useCallback(async () => {
    if (!address) {
      setValue(NON_LOADING_DEFAULT_BASIC_STATE);
      return;
    }

    const number = await contract.balanceOf(address);

    setValue({
      isLoading: false,
      value: number
    })

  }, [address, contract]);

  useEffect(() => {
    if (core) {
      fetchData().catch((err) => {
        setValue(NON_LOADING_DEFAULT_BASIC_STATE);
        console.error(`Failed to Number of NFT's: ${err}`);
      });
    }
  }, [setValue, core, address, fetchData]);

  return value;
};

export default useGetNumberOfNFTs;
