import {Grid} from '@material-ui/core'
import {BigNumber} from 'ethers'
import {useState} from "react";
import Div from "../../../components/Div";
import useStakeNFT from "../../../hooks/callbacks/Minting/useStakeNFT";
import useUnStakeNFT from "../../../hooks/callbacks/Minting/useUnStakeNFT";
import {IState} from "../../../hooks/state/Minting/useGetAllNFTs";
import useGetNFTStaked from "../../../hooks/state/Minting/useGetNFTStaked";
import {ITxStatus} from "../../../Modals/MintModal/MintWithMaha";
import {getDisplayBalance} from '../../../utils/formatBalance'

interface IProps {
  data: IState;
  onEvolve: (data: BigNumber) => void;
  isMyNFT: boolean;
}

const NftCard = (props: IProps) => {
  const {id, lockAmount, imagePath} = props.data;
  const [txStatus, setTxStatus] = useState<ITxStatus>('initial');
  const stakedStatus = useGetNFTStaked(id);

  const stakeAction = useStakeNFT(id);
  const handleStake = () => {
    stakeAction(
      () => {
        setTxStatus('in-progress')
      },
      () => {
        setTxStatus('successful')
        window.location.reload();
      },
      () => {
        setTxStatus('not-successful')
      },
    ).then(() => {
    })
  }

  const unStakeAction = useUnStakeNFT(id);
  const handleUnStake = () => {
    unStakeAction(
      () => {
        setTxStatus('in-progress')
      },
      () => {
        setTxStatus('successful')
        window.location.reload();
      },
      () => {
        setTxStatus('not-successful')
      },
    ).then(() => {
    })
  }

  return (
    <Grid item xs={12} sm={6} md={2} lg={2}>
      <div className={'nft-card'}>
        <Div onClick={() => window.open(`/#/gallery/${id.toString()}`, '_self')} trackingid={`nft-item`}
             trackingParams={{position: 'profile-page'}}>
          <div className={'nft-image'}>
            <img src={imagePath} alt={`nftId-${id.toString()}`}/>
          </div>
          <div className={'nft-details'}>
            <div className={'nft-details-row'}>
              <p className={'nft-id body-2'}>Citizen #{id.toString()}</p>
            </div>
            <div className={'nft-details-row'}>
              <div>
                <span
                  className={'maha-value body-4'}>{Number(getDisplayBalance(lockAmount, 18, 3)).toLocaleString()} MAHA</span>
              </div>
            </div>
          </div>
        </Div>
        {
          props.isMyNFT &&
          (stakedStatus.isLoading
            ? <div/>
            : !stakedStatus.value ?
              <Div trackingid={'stake-nft'} trackingParams={{position: 'profile-page'}} className={'evolve-nft-btn'}
                   onClick={handleStake}>
                Stake NFT
              </Div>
              : <Div trackingid={'unstake-nft'} className={'evolve-nft-btn'} trackingParams={{position: 'profile-page'}}
                     onClick={handleUnStake}>
                UnStake NFT
              </Div>)
        }
        {/* <Div trackingid={'evolve-nft'} className={'evolve-nft-btn'} onClick={() => props.onEvolve(id)}>
          Evolve NFT
        </Div>*/}
      </div>
    </Grid>
  )
}

export default NftCard;
