import Button from "../../../components/Button"
import bag_pink from "../../../images/icons/bag_pink.svg"
import discord_blue from "../../../images/icons/discord_blue.svg"
import lock_blue from "../../../images/icons/lock_blue.svg"
import star_red from "../../../images/icons/star_red.svg"
import MAHARAJA_10 from "../../../images/maharaja/MAHARAJA_10.svg"
