import React, {useState} from "react";
import {useAccount} from "wagmi";
import Button from "../../../components/Button";
import MintingModal from "../../../Modals/MintModal/MintingModal";
import Nfts from "./Nfts";

interface IProps {
  urlAddress: string;
  nftCount: number;
}

const Collection = (props: IProps) => {
  const {urlAddress} = props;
  const {address} = useAccount();

  const [openFuseModal, setOpenFuseModal] = useState<boolean>(false);
  const [mintModalOpen, setMintModalOpen] = useState<boolean>(false);

  return (
    <div className={'collection-section'}>
      <div className={'head'}>
        <div className={'title'}>
          <span className={'body-1'}>Citizens</span>
        </div>
        <div className={'right-head'}>
          <div className={'action-button'}>
            {address === urlAddress &&
                <Button className={'mint-nft primary-button'} trackingid={'mint-nft'}
                        trackingParams={{position: 'profile-page'}} onClick={() => setMintModalOpen(true)}>
                    Mint NFT
                </Button>
            }
          </div>
          {/*{address === urlAddress &&
            <button className={'secondary-button fuse-nft-btn'} onClick={() => setOpenFuseModal(true)}
              disabled={nftCount < 2}>
              Fuse NFT's
            </button>
          }*/}
        </div>
      </div>
      <MintingModal open={mintModalOpen} handleClose={() => setMintModalOpen(false)}/>
      {/*<FuseNFT openModal={openFuseModal} onModalClose={() => setOpenFuseModal(false)} urlAddress={urlAddress}/>*/}
      <Nfts urlAddress={urlAddress} openMintModal={() => setMintModalOpen(true)}/>
    </div>
  )
}

export default Collection;
