import {Grid} from "@material-ui/core";
import {useState} from "react";
import Loader from "react-spinners/BeatLoader";
import Button from "../../../components/Button";
import useStakeNFT from "../../../hooks/callbacks/Minting/useStakeNFT";
import useUnStakeNFT from "../../../hooks/callbacks/Minting/useUnStakeNFT";
import {INFTDetailsValue} from "../../../hooks/state/Minting/useGetNFTDetails";
import useGetNFTStaked from "../../../hooks/state/Minting/useGetNFTStaked";
import useHasChoosenTheTraits from "../../../hooks/state/Minting/useHasChoosenTheTraits";
import ChooseTraitsModal from "../../../Modals/ChooseTraits/ChooseTraitsModal";
import {ITxStatus} from "../../../Modals/MintModal/MintWithMaha";
import {useGetAccount} from "../../../utils/NetworksCustomHooks";
import NFTInfoSection from "./NFTInfoSection";

interface IProps {
  nftDetail: INFTDetailsValue;
  onEvolvePress: () => void;
}


const NFTDetailsSection = (props: IProps) => {
  const {nftDetail} = props;
  const account = useGetAccount();
  const [txStatus, setTxStatus] = useState<ITxStatus>('initial');
  const stakedStatus = useGetNFTStaked(nftDetail.id);
  const hasSelected = useHasChoosenTheTraits(nftDetail.id);
  const [openChooseTraits, setOpenChooseTraits] = useState<boolean>(false);

  const stakeAction = useStakeNFT(nftDetail.id);
  const handleStake = () => {
    stakeAction(
      () => {
        setTxStatus('in-progress')
      },
      () => {
        setTxStatus('successful')
        window.location.reload();
      },
      () => {
        setTxStatus('not-successful')
      },
    ).then(() => {
    })
  }

  const unStakeAction = useUnStakeNFT(nftDetail.id);
  const handleUnStake = () => {
    unStakeAction(
      () => {
        setTxStatus('in-progress')
      },
      () => {
        setTxStatus('successful')
        window.location.reload();
      },
      () => {
        setTxStatus('not-successful')
      },
    ).then(() => {
    })
  }

  const historyCount = nftDetail.userNft ? nftDetail.userNft.history: 0;

  return (
    <div className={'nft-details-section'}>
      <ChooseTraitsModal open={openChooseTraits} handleClose={() => setOpenChooseTraits(false)} nftId={nftDetail.id}/>
      <Grid container spacing={2}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <div className={'details-left'}>
            <div className={'nft-img'}>
              <img src={nftDetail.userNft ? nftDetail.userNft.imageUrl : ''} alt={'nftId-2342'}/>
            </div>
            {historyCount > 0 && <div className={'nft-history-disclaimer'}>
              <p>* You will be able to revert your PFP to a previous version if you do not like it.</p>
            </div>}
          </div>
        </Grid>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <div className={'details-right'}>
            <NFTInfoSection nftDetail={nftDetail}/>
            {
              nftDetail.ownedBy === account &&
                <Grid container spacing={2}>
                    <Grid item lg={6} md={6} sm={12} xs={12}>
                      {stakedStatus.isLoading
                        ? <Loader size={8} color={'black'}/>
                        : stakedStatus.value
                          ? <Button trackingid="unstake-nft" className={'primary-button evolve-nft'}
                                    trackingParams={{position: 'nft-details-page'}} onClick={handleUnStake}
                                    disabled={txStatus === 'in-progress'}>
                            UnStake NFT
                          </Button>
                          : <Button trackingid="unstake-nft" className={'primary-button evolve-nft'}
                                    trackingParams={{position: 'nft-details-page'}} onClick={handleStake}
                                    disabled={txStatus === 'in-progress'}>
                            Stake NFT
                          </Button>}
                    </Grid>
                    <Grid item lg={6} md={6} sm={12} xs={12}>
                      {hasSelected.isLoading
                        ? <Loader size={8} color={'black'}/>
                        : <Button trackingid="choose-traits" className={'primary-button evolve-nft'}
                                  trackingParams={{position: 'nft-details-page'}}
                                  onClick={() => setOpenChooseTraits(true)}
                                  disabled={txStatus === 'in-progress' || hasSelected.value}>
                          Choose Traits
                        </Button>
                      }
                    </Grid>
                </Grid>


            }
            {/*<div>
              {nftDetail?.value?.ownedBy === address &&
                  <Button trackingid="evolve-nft" className={'primary-button evolve-nft'} onClick={onEvolvePress}>
                      Evolve NFT
                  </Button>
              }
            </div>*/}
          </div>
        </Grid>
      </Grid>


    </div>
  )
}

export default NFTDetailsSection;
