import {Grid} from "@material-ui/core";
import {BigNumber} from "ethers";
import React, {useState} from "react";
import {Oval} from 'react-loader-spinner';
import Div from "../../../components/Div";
import useGetAllNFTs from "../../../hooks/state/Minting/useGetAllNFTs";
import EvolveNFTModal from "../../../Modals/EvolveNFT";
import {BNZERO} from "../../../utils/constants";
import {useGetAccount} from "../../../utils/NetworksCustomHooks";
import NftCard from "./NftCard";

interface IProps {
  urlAddress: string;
  openMintModal: () => void;
}

const Nfts = (props: IProps) => {
  const {urlAddress} = props;
  const account = useGetAccount();
  const allNFts = useGetAllNFTs(urlAddress);
  const [openEvolveNFT, setOpenEvolveNFT] = useState<boolean>(false);
  const [nftId, setNftId] = useState<BigNumber>(BNZERO);

  return (
    <div className={'nfts '}>
      <EvolveNFTModal nftId={nftId} modalOpen={openEvolveNFT} onModalClose={() => setOpenEvolveNFT(false)}/>
      {allNFts.isLoading
        ?
        <div className="flex-center">
          <Oval height={80}
                width={80}
                color="#2e4d6e"
                visible={true}
                ariaLabel='oval-loading'
                secondaryColor="#2e4d6e"
                strokeWidth={2}
                strokeWidthSecondary={2}/>
        </div>
        : !allNFts.isLoading && allNFts.value.length === 0
          ?
          <div className="flex-center">
            You have no citizenship. <Div trackingid={'get-citizenship'} onClick={() => {
            props.openMintModal()
          }} className='pointer ml5 links'>Get your own citizenship.</Div>
          </div>
          :
          (<Grid container spacing={2}>
            {allNFts.value.map((data) => {
              return data.id.gt(0)
                ? <NftCard
                  key={data.id.toString()}
                  data={data}
                  isMyNFT={urlAddress === account}
                  onEvolve={(id) => {
                    /*setNftId(id)
                    setOpenEvolveNFT(true);*/
                  }}
                />
                : <div/>
            })}
          </Grid>)
      }
    </div>
  )
}

export default Nfts;
