import numeral from 'numeral'
import {useCallback, useEffect, useState} from "react";
import {useAccount} from "wagmi";
import useGetNativeTokenBalance from "../hooks/state/useGetNativeTokenBalance";
import useCore from "../hooks/useCore";
import useTokenBalance from "../hooks/useTokenBalance";
import {useGetActiveChainId} from "../state/chains/hooks";
import {getDisplayBalance} from "./formatBalance";


export const truncateMiddle = function (
  fullStr: string = '',
  strLen: number,
  separator?: string,
) {
  if (fullStr.length <= strLen) return fullStr;
  separator = separator || '...';

  var sepLen = separator.length,
    charsToShow = strLen - sepLen,
    frontChars = Math.ceil(charsToShow / 3),
    backChars = Math.floor(charsToShow / 3);

  return fullStr.substr(0, frontChars) + separator + fullStr.substr(fullStr.length - backChars);
};

const marksArr = [
  {
    value: 100,
    label: '0%',
  },
  {
    value: 1000,
    label: '100%',
  },
]

export const useGetSliderMarks = (token: string) => {
  const core = useCore()
  const {isConnected, address, status} = useAccount()

  const activeChainId = useGetActiveChainId()
  const tokenMahaBal = useTokenBalance(core._tokens[activeChainId]['MAHA'])
  const tokenEthBal = useGetNativeTokenBalance()

  let finalTokenBal =
    !isConnected ? 1000 :
      token === 'MAHA' ?
        !tokenMahaBal.isLoading ? Number(getDisplayBalance(tokenMahaBal.value, 18, 6)) : 0 :
        !tokenEthBal.isLoading ? Number(getDisplayBalance(tokenEthBal.value, 18, 6)) : 0

  const fetchMarks = useCallback(async () => {
    if (core.myAccount) {
      if (token === 'MAHA') {
        if (!tokenMahaBal.isLoading && Number(tokenMahaBal.value) > 0) {
          setMarks([
            {
              value: 100,
              label: '0%',
            },
            {
              value: Number(finalTokenBal),
              label: '100%',
            },
          ])
        } else {
          setMarks([
            {
              value: 100,
              label: '0%',
            },
            {
              value: 1000,
              label: '100%',
            },
          ])
        }
      } else {
        if (!tokenEthBal.isLoading && Number(tokenEthBal.value) > 0) {
          setMarks([
            {
              value: 0.04,
              label: '0%',
            },
            {
              value: Number(finalTokenBal),
              label: '100%',
            },
          ])
        } else {
          setMarks([
            {
              value: 0.04,
              label: '0%',
            },
            {
              value: 1000,
              label: '100%',
            },
          ])
        }
      }
    }


  }, [core.myAccount, finalTokenBal, token, tokenEthBal.isLoading, tokenEthBal.value, tokenMahaBal.isLoading, tokenMahaBal.value])

  const [marks, setMarks] = useState<any[]>([])

  useEffect(() => {

    fetchMarks().catch((err) => console.log('fetchMarks err', err))

  }, [finalTokenBal, tokenMahaBal.isLoading, tokenMahaBal, tokenEthBal, tokenEthBal.isLoading, core])


  return marks
}


export const formatTooltipVal = (val: number) => {
  let string = numeral(val).format('0.00');

  return string

}

export const formatTooltipEthVal = (val: number) => {
  let string = numeral(val).format('0.0000');

  return string
}
