import {Grid} from "@material-ui/core";
import moment from 'moment'
import Anchor from "../../../components/Anchor";
import {INFTDetailsValue, ITraits} from "../../../hooks/state/Minting/useGetNFTDetails";
import {truncateMiddle} from "../../../utils";
import {getDisplayBalance} from "../../../utils/formatBalance";

interface IProps {
  nftDetail: INFTDetailsValue;
}

const NFTInfoSection = (props: IProps) => {
  const {nftDetail} = props

  function capitalizeFirstLetter(value: string) {
    return value.charAt(0).toUpperCase() + value.slice(1);
  }

  function getTraitNameFormatted(value: string) {
    const array = value.split('_');
    const name = array.map((data) => {
      return capitalizeFirstLetter(data)
    })
    return name.join(' ');
  }

  return (
    <div>
      <section>
        <div>
          <p className={'nft-name'}>Citizen #{nftDetail.id.toString()}</p>
          <div className={'nft-owner'}>Owned by <Anchor trackingid="view-profile"
                                                        trackingParams={{position: 'nft-details-page'}}
                                                        className="pointer"
                                                        href={`/#/profile/${nftDetail.ownedBy}`}> {truncateMiddle(nftDetail.ownedBy, 16, '...')} </Anchor> |
            Locked
            for {moment(new Date(Number(`${nftDetail.lockEnds?.toString()}000`))).fromNow(true)} |
            <Anchor trackingid={'view-on-opensea'} trackingParams={{position: 'nft-details-page'}} className={'pointer'}
                    href={`https://opensea.io/assets/ethereum/0xbdd8f4daf71c2cb16cce7e54bb81ef3cfcf5aacb/${nftDetail.id}`}
                    target={'_blank'}> OpenSea</Anchor>
          </div>
        </div>
      </section>
      <section>
        <Grid container spacing={3}>
          <Grid item lg={6} md={6} sm={6} xs={12}>
            <div className={'value-info'}>
              <p className={'value-title'}>Locked Amount</p>
              <p
                className={'title-info'}>{Number(getDisplayBalance(nftDetail.lockAmount, 18, 3)).toLocaleString()} MAHA</p>
            </div>
          </Grid>
          <Grid item lg={6} md={6} sm={6} xs={12}>
            <div className={'value-info'}>
              <p className={'value-title'}>Worth</p>
              <p
                className={'title-info'}> {Number(getDisplayBalance(nftDetail.lockValue, 18, 3)).toLocaleString()} MAHAX</p>
            </div>
          </Grid>
        </Grid>
      </section>
      <section>
        <p className={'traits-heading'}> Traits </p>
        <Grid container spacing={2}>
          {nftDetail.userNft &&
            nftDetail.userNft.attributes.map((item: ITraits, index) => {
              return <Grid item lg={6} md={6} sm={6} xs={6} key={index}>
                <div className={'traits-info'}>
                  <p className={'traits-title'}>{capitalizeFirstLetter(item.category)} </p>
                  <p className={'title-info'}>{getTraitNameFormatted(item.trait)}</p>
                </div>
              </Grid>
            })
          }
        </Grid>
      </section>
    </div>
  )
}

export default NFTInfoSection;
