import {BigNumber} from "ethers";
import React, {useEffect, useState} from "react";
import {useMediaQuery} from "react-responsive";
import Button from "../../components/Button";

import CustomModal from "../../components/Modal";

import "../../customCss/Custom-Slider.css"
import useChooseTraits from "../../hooks/callbacks/Minting/useChooseTraits";
import useLockMAHA from "../../hooks/callbacks/Minting/useLockMAHA";

import ModalClose from "../../images/icons/ModalClose.svg";
import faq_divider_mob from "../../images/vectors/faq_divider_mob.svg";
import modal_divider from "../../images/vectors/modal_divider.svg";
import {BNZERO} from "../../utils/constants";
import FailureModal from "./FailureModal";
import MintingInProgressModal from "./MintingInProgressModal";
import "./MintModal.css"
import SkinGenderSelection, {IGender, ISkin} from "./SkinGenderSelection";
import SuccessfulMintModal from "./SuccessfulMintModal";

interface IProps {
  open: boolean;
  handleClose: () => void;
  nftId: BigNumber;
}

export type ITxStatus = 'initial' | 'not-initiated' | 'in-progress' | 'successful' | 'not-successful' | '';

const ChooseTraitsModal = (props: IProps) => {
  const isMobile = useMediaQuery({query: '(max-width: 768px)'})
  const [txStatus, setTxStatus] = useState<ITxStatus>('initial');
  const [failureMsg, setFailureMsg] = useState<string | undefined>('');
  const [gender, setGender] = useState<IGender>('');
  const [skin, setSkin] = useState<ISkin>('');

  useEffect(() => {
    if (!props.open) {
      setTxStatus('initial');
    }
  }, [props.open])

  const lockMAHAAction = useChooseTraits(
    props.nftId,
    gender,
    skin
  )

  const handleChooseTraits = () => {
    lockMAHAAction(() => {
        setTxStatus('in-progress')
      },
      () => {
        setTxStatus('successful')
      },
      (msg: string) => {
        setTxStatus('not-successful')
        setFailureMsg(msg);
      }
    ).then(() => {
      console.log("inside then")
    })
  }

  return (
    <div>
      <MintingInProgressModal open={txStatus === 'in-progress'} handleClose={() => setTxStatus('')}/>
      <SuccessfulMintModal open={txStatus === 'successful'} nftid={props.nftId.toString()} handleClose={() => {
        setTxStatus('');
        props.handleClose();
        window.location.reload();
      }}/>
      <FailureModal open={txStatus === 'not-successful'} handleClose={() => props.handleClose()}
                    msg={failureMsg === '' ? undefined : failureMsg}/>
      <CustomModal open={props.open && txStatus === 'initial'} onClose={props.handleClose}>
        <div>
          <div className='mint-modal-heading'>
            <div>Choose Traits</div>
            <div className='pointer' onClick={props.handleClose}><img src={ModalClose} alt={'ModalClose'}/></div>
          </div>
          <div className='text-center mb16'>
            {
              isMobile ?
                <img src={faq_divider_mob} alt={'faq_divider_mob'} className={'responsiveImg'}/> :
                <img src={modal_divider} alt={'modal_divider'} className={'responsiveImg'}/>
            }
          </div>
          <div>
            <SkinGenderSelection gender={gender} skin={skin} setGender={setGender} setSkin={setSkin}/>
            {
              (gender === '' || skin === '') &&
                <p className={'error-msg text-center'}>* You need to choose a gender and skin color</p>
            }
            <div data-rk={"true"} className={'mint-nft-btn'}>
              {
                (gender !== '' && skin !== '') && (
                    <Button type="button" trackingid={'choose-traits-modal'}
                            trackingParams={{value: Number(props.nftId), gender: gender, skin: skin}}
                            className='primary-button primary-modal-button-text' onClick={handleChooseTraits}>
                      Confirm
                    </Button>
                )
              }
            </div>
          </div>
        </div>
      </CustomModal>
    </div>
  )
}

export default ChooseTraitsModal;
