import React from "react";
import {useMediaQuery} from "react-responsive";
import Button from "../../../components/Button";
import Div from "../../../components/Div";
import mahadaoLogoMob from '../../../images/mahadao-logo-mob.png';
import mahadaoLogo from '../../../images/mahadao-logo.png';
import heroGIF from '../../../images/new-nft-hero.gif';
import cloudMdMob from '../../../images/vectors/cloud-md-mob.png';
import cloudMd from '../../../images/vectors/cloud-md.png';
import cloudSmMob from '../../../images/vectors/cloud-sm-mob.png';
import cloudSm from '../../../images/vectors/cloud-sm.png';
import '../style/HeroSection.css';
import FreeMintCountdown from "./FreeMintCountdown";

const HeroSection = () => {
  const isMobile = useMediaQuery({query: '(max-width: 768px)'})

  const handleScrollToElement = () => {
    const node = document.getElementById('mint-with')
    if (node) window.scrollTo(0, node.offsetTop);
  }

  return (
    <div className={'hero-section'}>
      <div className={'main'}>
        <div className={'cloud-sm'}><img src={isMobile ? cloudSmMob : cloudSm} alt={'cloud-sm'}/></div>
        <div className={'cloud-md'}><img src={isMobile ? cloudMdMob : cloudMd} alt={'cloud-md'}/></div>
        <div className={'image'} data-aos="zoom-in" data-aos-duration="500" data-aos-delay="200">
          <img src={heroGIF} alt={'hero-gif'} className={'img-gif'}/>
        </div>
        <div className={'content'}>
          <Div className={'logo'} trackingid={'mahadao'} trackingParams={{position: 'hero-section'}}
               onClick={() => window.open('https://mahadao.com', '_blank')}>
            <img src={isMobile ? mahadaoLogoMob : mahadaoLogo}/>
          </Div>
          <div className={'people-word'}>
            <span className={`${isMobile ? 'heading-5' : 'heading-1'}`}>PEOPLE</span>
          </div>
          <div className={'second-line'}>
            <div className={'of-box'}>
              <div className={'horizontal-line'}/>
              <span className={'of'}>OF</span>
              <div className={'horizontal-line'}/>
            </div>
            <div className={'eden-word'}>
              <span className={isMobile ? 'heading-5 mt16 mb24' : 'heading-1'}>EDEN</span>
            </div>
          </div>
          <div className={'call-to-action'}>
            <Button trackingid="get-citizenship" className={`primary-button ${!isMobile && 'mr16'}`}
                    onClick={() => handleScrollToElement()}>
              GET CITIZENSHIP
            </Button>
            <Button trackingid="join-discord" className={`secondary-button ${!isMobile && 'mr16'}`}
                    trackingParams={{position: 'hero-section'}}
                    onClick={() => window.open('https://discord.com/invite/mahadao', '_blank')}>
              JOIN DISCORD
            </Button>
            {/*<Button trackingid="join-whitelist" className={`secondary-button`}
                    onClick={() => window.open('https://www.alphabot.app/people-of-eden-mahax-spot-r4hiyy', '_blank')}>
              JOIN WHITELIST
            </Button>*/}
          </div>
          <FreeMintCountdown/>

        </div>
      </div>
    </div>
  )
}

export default HeroSection;
